.backdrop {
  position: fixed;
  inset: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 999;
  @media (min-width: 640px) {
    .container {
        max-width: 640px;
    }
}
  @media screen and (min-width: 768px){
    display: none;
  }
}

.backdrop.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.modal {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
}

.close-modal {
  position: fixed;
  width: 20px;
  height: 20px;
  right: 31px;
    top: 18px;
}

.close-item { 
  position: absolute;
   height: 2px;
   width: 20px;
   background: #fff;
   transform: rotate(45deg);
}

.close-item::before { 
  position: absolute;
  content: '';
     height: 2px;
   width: 20px;
   background: #fff;
   right: 0;
   transform: rotate(-90deg);
}

