.header {
  padding-top: 20px;
  @media screen and (min-width: 768px) {
    padding-top: 45px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 60px;
  }
}

.burger {
  display: none;
  position: relative;
  align-items: center;
  width: 18px;
  height: 16px;
  z-index: 2;
  cursor: pointer;
  @media (max-width: 767px) {
    display: flex;
  }
}

.burger span {
  height: 2px;
  width: 100%;
  transform: scale(1);
  background-color: #fcfcfb;
}
.burger::before,
.burger::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #fcfcfb;
  transition: all 0.3s ease 0s;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}

.name-header {
  position: relative;
  z-index: 1000;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2%;
  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 40px;
  }
}

.item-nav {
    position: relative;
  margin-bottom: 60px;
  @media screen and (min-width: 768px) {
    margin: 0;
  }
}

.item-nav::after {
  @media screen and (max-width: 767px) {
    content: '';
    width: 200px;
    display: block;
    margin: 0 auto;
    height: 1px;
    background-color: #fff;
    margin-top: 20px;
  }
}

.btn-nav {
  cursor: pointer;
  color: rgb(255, 255, 255);
  font-size: 30px;
  font-weight: 600;
  line-height: 22px;

  @media screen and (min-width: 768px) {
    font-size: 18px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}

.btn-border {
  color: #fff;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 30px;
  font-size: 16px;
  font-weight: 600;
  padding: 7px 15px;
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    padding: 14px 32px;
  }
}

.btn-modal {
  color: #fff;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 30px;
  font-weight: 600;
  font-size: 20px;
  padding: 14px 32px;
  display: grid;
  width: 151px;
  margin: 0 auto;
  justify-content: center;
  margin-top: 70px;
}
